:root {
  --sunrise-yellow: #ffde16;
  --sunrise-charcoal: #33342e;
  --sunrise-magenta: #8f0d56;
  --sunrise-orange: #fd9014;
  --sunrise-font: Source Sans Pro;
  --form-gutter-size: 33.33%;
}

html {
  font-family: var(--sunrise-font);
  background-size: 200%;
  background-position: center;
  background-color: black;
}

body {
  width: 100%;
  height: 100%;
  margin: 0;
}

* {
  box-sizing: border-box;
}

section#video {
  height: 66.7vh;
  position: relative;
  background-color: #000;
}

section#video youtube-video {
  width: 100%;
  height: 100%;
  opacity: 0;
  pointer-events: none;
}

section#video youtube-video[player-state="playing"] {
  opacity: 1;
}

section#lead-in {
  background-color: var(--sunrise-charcoal);
  color: var(--sunrise-yellow);
}
  section#lead-in article {
    width: calc(100% - var(--form-gutter-size));
    padding: 32px;
  }
  section#lead-in h2 {
    margin: 0;
    font-size: 72px;
    line-height: 1;
    text-transform: uppercase;
  }
  section#lead-in p {
    font-size: 24px;
  }

section#questions {
  background-color: var(--sunrise-magenta);
  background-image: linear-gradient(
    to bottom,
    var(--sunrise-magenta) 0%,
    var(--sunrise-orange) 40%,
    var(--sunrise-yellow) 80%,
    var(--sunrise-yellow) 100%
  );
}
  section#questions article {
    width: calc(100% - var(--form-gutter-size));
    padding: 32px;
  }
  section#questions h2 {
    color: var(--sunrise-yellow);
    font-size: 64px;
    line-height: 1;
    margin: 0;
    text-transform: uppercase;
  }
  section#questions dl {
    font-size: 24px;
  }
  section#questions dt {
    font-weight: 700;
    margin-bottom: 12px;
  }
  section#questions dd {
    margin: 0;
    margin-bottom: 24px;
  }

main {
  position: relative;
  display: grid;
  grid-template-columns: calc(100% - var(--form-gutter-size)) var(--form-gutter-size);
  grid-template-rows: repeat(auto-fill, auto);
}
  main section {
    grid-column: 1 / span 2;
  }
  main section#lead-in {
    grid-row: 1;
  }
  main section#questions {
    grid-row: 2;
  }

section#signup-form {
  grid-column: 2 / span 1;
  grid-row: 1 / span 2;
  position: relative;
  padding: 0 16px;
}
  section#signup-form action-network-form {
    position: sticky;
    top: 16px;
    border-radius: 8px;
    overflow: hidden;
    box-shadow: 0px 2px 12px 2px rgba(0,0,0,0.25);
    max-height: calc(100vh - 32px);
    margin-top: -32px;
    height: 100%;
  }